html, body, #root{
  margin: 0;
  padding: 0;
  height: 100%;
  text-align: center;
  color: darkcyan;
  background-image: linear-gradient(45deg, lightblue, white); 
}


a{
  color: blue;
}

.Box{
  display: inline-block;
  text-align: left;
  border: 2px dashed black;
  padding: 5px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px;
}
.SolidBox{
  display: inline-block;
  text-align: left;
  border: 2px solid black;
  padding: 5px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px;
}